<template>
    <div class="item_bank">
      <div class="item_bank_title">
        <div class='item_bank_title_name'>
          <span class="first-head-word">匠心</span><span class="scecond-head-word">配网</span>
        </div>
        <div class='item_bank_title_search'>
            <div class="search-div" @click="enterSearch()">
              <img src="../../assets/image/放大镜.png" alt />
              <span>关键词、内容检索</span>
            </div>
        </div>
      </div>
        <!-- 下拉刷新 -->
        <van-pull-refresh class='item_container' v-model="isLoading" @refresh="onRefresh()">
          <!-- 广告轮播 -->
          <div class="item_bank-header">
              <van-swipe :autoplay="3000" v-if = 'adLength == true' class='swiperImg' indicator-color="white">
                  <van-swipe-item v-for="(item, index) in advertisingList" :key="index"  @click.stop='enterAD(item.adType,item.linkId,item.specialCode,item.linkName,item.id,item.url,item.fileType)'>
                      <img v-lazy="item.img" />
                  </van-swipe-item>
              </van-swipe>
              <div class="swiperImg" v-if = 'adLength == false'>
                  <div class="swiper-wrapper">
                      <div class="swiper-slide"><img src="../../assets/image/没有广告.png" alt=""></div>
                  </div>
              </div>
          </div>
          <div class='item_bank_container'>
              <div class='item_bank_top'>
                  <div class='text_remind'>
                      <div class='remind_box' @click.stop="enterText()">
                          <img class='remind_boximg' src="../../assets/image/主页提醒.png" alt="">
                          <div class='remind_name'>{{epName}}</div>
                          <!-- <img class='remind_enter' src="../../assets/image/考试提醒进入.png" alt=""> -->
                      </div>
                  </div>
              </div>
              <div class='item_bank_bottom'>
                  <div class='item_bank_news'>
                      <div class='news_top'>
                          <div class='news_title'>新闻</div>
                          <div class='news_enter' @click.stop='enterNews()'><img src="../../assets/image/back.png" alt=""></div>
                      </div>
                      <div class='news_container'>
                          <!-- 新闻列表 -->
                          <div class='news_list' v-for='(item) in newsListData' :key='item.id' @click.stop='enterNewsDetail(item.id)'>
                            <div class='news_list_top' style="padding-bottom:0.15rem;color:#4E4E4E;font-size:0.34rem;">{{item.title}}</div>
                              <div class='news_img'>
                                  <img src="../../assets/image/新闻图.png" alt="" v-if='!item.newsImg'>
                                  <img :src="item.newsImg" alt="" v-if='item.newsImg'>
                              </div>
                              <div class='news_list_right'>

                                  <div class='news_list_bottom'>
                                      <div class='news_from'>{{item.source}}</div>
                                      <div class='news_time'>{{item.publishTime}}</div>
                                  </div>
                              </div>
                          </div>
                          <!-- 新闻为空时 -->
                          <div class='newsEmply'>
                              <img src="../../assets/image/emply.png" alt="">
                              <div>空空如也~</div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
        </van-pull-refresh>
        <!-- 加载页面 -->
        <itemLoading :dialogVisible='isShowLoading'></itemLoading>
    </div>
</template>
<script>
import itemLoading from './../components/itemLoading'
import API from '@/api/api'
import $ from 'jquery'
import qs from 'qs'
export default {
  components: {
    itemLoading
  },
  data () {
    return {
      isShowLoading: true,
      epName: '', // 考试通知
      id: '', // 考试id
      advertisingList: [], // 广告轮播图
      adLength: true, // 广告是否存在
      NewMate: [], // 最新资讯
      newsListData: [], // 新闻列表
      isLoading: false, // 下拉刷新
      findAdvanceSearchCG: true, // 广告接口是否获取成功
      getfindNewsBaseCG: true // 获取新闻接口是否获取成功
    }
  },
  created () {
    localStorage.removeItem('content') // 清空存储搜索内容的缓存
    this.findAdvanceSearch() // 获取广告接口
    this.getfindNewsBase() // 获取新闻接口
  },

  mounted () {
    console.log('store', this.$store.state.spe)
    this.$parent.isShow = 0 // 底部栏显示第一个高亮
  },
  watch: {
    // 监听是否所有接口success而关闭加载页
    requestSuccess (val, newval) {
      if (val) {
        // this.itemverDialogVisible = false;
        this.isShowLoading = false // 加载页消失
        setTimeout(() => {
          // this.$toast('刷新成功');
          this.isLoading = false
        }, 500)
      } else {
        setTimeout(() => {
          // this.$toast('刷新失败');
          this.isLoading = false
        }, 40000)
      }
    }
  },
  computed: {
    requestSuccess () {
      return this.findAdvanceSearchCG && this.getfindNewsBaseCG
    }
  },
  methods: {
    onRefresh () {
      this.findAdvanceSearch() // 获取广告接口
      this.getfindNewsBase() // 获取新闻接口
    },
    // 进入视频详情
    enterVideoDetail (id, name, specialCode) {
      API.post('findVideoDetilsById', {
        deflag: '0',
        id: id
      }, 'json', (data, err) => {
        if (data.success) {
          if (data.data) {
            this.$router.push({
              name: 'video_detail',
              params: { id: id, name: name, specialCode: specialCode }
            })
          } else {
            this.$toast('该视频不存在')
            this.findNewMate()
          }
        }
      })
    },
    // 进入新闻
    enterNewsDetail (id) {
      API.post('findNewsDetilById', {
        id: id,
        status: '1302'
      }, 'json', (data, err) => {
        if (data.success) {
          if (data.data) {
            this.$router.push({
              name: 'news_detail',
              params: { id: id }
            })
          } else {
            this.$toast('该新闻不存在')
            this.getfindNewsBase()
          }
        }
      })
    },
    // 进入搜索
    enterSearch () {
      localStorage.setItem('enterIndex', this.GLOBAL.ENTERSOURCE.newsSearch)
      this.$router.push({
        name: 'search'
      })
    },
    // 进入新闻列表
    enterNews () {
      this.$router.push({
        name: 'news_list'
      })
    },
    // 新闻列表
    getfindNewsBase () {
      this.getfindNewsBaseCG = false
      API.post('findNewsBase', {
        pageMsg: {
          'currentPage': this.GLOBAL.NEWS.PAGE_NUM,
          'pageSize': this.GLOBAL.NEWS.PAGE_SIZE_STUDY },
        queryFilter: {
          'status': '1302'
        }
      }, 'json', (data, err) => {
        if (data.success) {
          this.forBackData(data.data.records)
          this.getfindNewsBaseCG = true
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 循环返回的数据
    forBackData (list) {
      this.newsListData = []
      for (var i = 0; i < list.length; i++) {
        list[i].publishTime = this.GLOBAL.changeTime(list[i].publishTime) // 新闻发布时间转化
        this.newsListData.push(list[i]) // 新闻列表
      }
      if (this.newsListData.length === 0) {
        $('.newsEmply').css('display', 'flex')
        $('.news_list').css('display', 'none')
        $('.news_enter').css('display', 'none')
      } else {
        $('.newsEmply').css('display', 'none')
        $('.news_list').css('display', 'flex')
        $('.news_enter').css('display', 'flex')
      }
    },
    // 广告接口
    findAdvanceSearch () {
      this.findAdvanceSearchCG = false
      API.post('findAdvanceSearch', {
        pageMsg: {
          'currentPage': 1, 'pageSize': 5
        },
        queryFilter: {
          status: this.GLOBAL.AD.status
        }
        // status: this.GLOBAL.AD.status
      }, 'json', (data, err) => {
        if (data.success) {
          this.advertisingList = data.data.records
          if (this.advertisingList.length > 0) {
            this.adLength = true
          } else {
            this.adLength = false
          }
          this.findAdvanceSearchCG = true
        } else {
          this.$toast(data.msg)
        }
      })
    },
    // 广告进入
    enterAD (adType, id, specialCode, name, adid,url,fileType) {
      API.post('findDetail', {
        id: adid
      }, 'json', (data, err) => {
        if (data.success) {
          if (adType === this.GLOBAL.AD.adNews) {
            this.$router.push({
              name: 'news_detail',
              params: { id: id }
            })
          } else if (adType === this.GLOBAL.AD.adText) {
            if (fileType === 'epub') {
              this.$router.push({
                name: 'text_train',
                params: { id: id, name: name, specialCode: specialCode }
              })
            } else {
              var u = navigator.userAgent // 判断是安卓还是ios
              var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1 // g
              var isIOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios终端
              if (isAndroid) {
                cordova.InAppBrowser.open(url, '_system', 'footer=yes,closebuttoncaption="返回",hideurlbar=yes,hidenavigationbuttons=yes,location=no')
              }
              if (isIOS) {
                cordova.InAppBrowser.open(url, '_system', 'footer=yes,closebuttoncaption="返回",hideurlbar=yes,hidenavigationbuttons=yes,location=no')
              }
            }
          } else if (adType === this.GLOBAL.AD.adVideo) {
            this.$router.push({
              name: 'video_detail',
              params: { id: id, specialCode: specialCode }
            })
          }
        } else {
          this.$toast(data.msg)
          this.findAdvanceSearch()
        }
      })
    }
  }
}
</script>
