        height:3.23rem;
        display: none;
        flex-direction: column;
        align-items: center;
        position: absolute;
        left:0;
        top:0.91rem;
        z-index: 2;
    }
    .booksEmply img{
        width:2.21rem;
        height:2rem;
        display: block;
    }
    .booksEmply div{
        color:#9FA7AF;
        font-size:0.26rem;
        margin-top:0.24rem;
        margin-left:0.02rem;
    }
</style>
<style>
    .item_bank .w-e-toolbar{
      display: none;
    }
    .item_bank .w-e-text-container{
      border:0 !important;
      z-index: 100 !important;
      font-size:initial;
      /* font-weight:initial; */
    }
    .item_bank .w-e-text-container b{
      font-weight:normal;
    }
    .item_bank .w-e-text{
      border:0;
      padding:0;
      font-size:initial;
      font-weight:initial;
    }
    .mint-toast{
      line-height: 0.4rem !important;
      z-index: 9999 !important;
    }
    .news_list_top{
      width:100%;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
</style>



